.catalog {
  display: grid;

  margin-bottom: 70px;

  grid-row-gap: 86px;
  grid-template-columns: 1fr;
  justify-items: center;
  grid-column-gap: 30px;

  &--order {
    margin-bottom: 0;
    padding-bottom: 25px;
  }
}

.catalog__item {
  display: flex;
  flex-direction: column;

  max-width: 291px;

  justify-content: flex-end;
}

.catalog__img {
  width: 100%;
  height: auto;
  margin-bottom: 17px;

  object-fit: contain;
}

.catalog__link {
  display: flex;

  text-decoration: none;

  justify-content: center;
}

.catalog__title {
  margin: 0;
  margin-bottom: 10px;

  text-decoration: none;

  font-size: 18px;
  line-height: 1.3em;

  color: $white;
}

.catalog__text {
  width: 100%;
  margin: 0;
  margin-bottom: 15px;

  text-decoration: none;

  font-size: 14px;

  color: $white;
}

.catalog__lower {
  display: flex;

  margin-bottom: 16px;

  gap: 27px;
  align-items: center;
  flex-wrap: wrap;
}

.catalog__price {
  position: relative;

  font-size: 24px;

  &::before {
    position: absolute;
    top: -15px;
    left: -10px;

    width: 2px;
    height: 50px;

    content: '';

    background-color: $orange;
  }
}

.catalog__buy {
  display: block;

  margin: 0 auto;
  margin-top: 20px;
  padding: 0 18px;

  cursor: pointer;

  font-family: $montserrat;
  font-size: 24px;

  border: none;
  border-radius: 5px;

  &:hover,
  &:focus {
    color: $white;
    background-color: $orange;
  }
}

.catalog__bottom {
  display: flex;

  gap: 20px;
  flex-wrap: wrap;
  align-items: center;
}

.catalog__final {
  display: flex;
  flex-direction: column;

  flex-wrap: wrap;
  align-items: center;
}

.catalog__final-price {
  display: block;

  margin-bottom: 20px;

  font-size: 30px;

  color: $white;
}

.catalog__final-button {
  width: 100%;
  max-width: 350px;
  margin-bottom: 25px;
  padding: 4px;

  text-align: center;

  font-family: $montserrat;
  font-size: 18px;

  border: none;
  border-radius: 5px;
  background-color: $white;
}

.catalog__item-order {
  position: relative;
}

.catalog__delete {
  cursor: pointer;

  border: none;
  background: transparent;

  svg {
    fill: $white;
    stroke: $white;
  }

  &:hover,
  &:focus {
    svg {
      fill: $orange;
      stroke: $orange;
    }
  }
}

@media(min-width:$widthTablet) {
  .catalog {
    grid-template-columns: 1fr 1fr;
  }
}

@media(max-width:1023px) {
  .catalog--order {
    .catalog__price {
      margin-right: 30px;
      margin-left: auto;
    }

    .catalog__number {
      display: flex;

      justify-content: flex-end;
    }

    .catalog__delete {
      position: absolute;
      bottom: 45px;
      left: 0;
    }
  }
}

@media(min-width:$widthDesktop) {
  .catalog {
    margin-bottom: 41px;

    grid-template-columns: 1fr;

    &--order {
      margin-bottom: 0;
      padding-bottom: 40px;

      .catalog__item {
        height: 181px;

        gap: 32px;
        grid-template-columns: 1fr 2fr;

        &:nth-of-type(2n) {
          .catalog__top {
            order: 0;
          }
        }
      }

      .catalog__img {
        width: 313px;
      }

      .catalog__bottom {
        align-items: center;
      }
    }
  }

  .catalog__item {
    display: grid;

    width: 100%;
    max-width: 100%;
    height: 281px;

    align-items: center;
    grid-template-columns: 1fr 1fr;
    gap: 43px;

    &:nth-of-type(2n) {
      .catalog__top {
        order: 1;
      }
    }
  }

  .catalog__bottom {
    position: relative;

    justify-content: flex-start;
    align-items: flex-start;
  }

  .catalog__title {
    max-width: 343px;

    font-size: 24px;
  }

  .catalog__text {
    width: 100%;
    max-width: 343px;
    margin-right: 30px;

    font-size: 16px;
  }

  .catalog__buy {
    margin: 0;

    order: 1;
  }

  .catalog__link {
    height: 100%;
  }

  .catalog__img {
    width: 100%;
    height: auto;
    max-height: 400px;
    margin-bottom: 0;

    object-fit: contain;
  }

  .catalog__number {
    width: 126px;
    margin-right: 80px;

    order: 1;
  }

  .catalog__final {
    flex-direction: row;

    width: 100%;

    justify-content: space-between;
  }

  .catalog__final-price {
    margin-bottom: 0;

    font-weight: 700;
  }

  .catalog__final-button {
    margin-bottom: 0;
  }

  .catalog__item-order {
    display: flex;
  }

  .catalog__delete {
    position: relative;
  }

  .catalog__final-button {
    font-size: 24px;
  }

  .catalog__price {
    position: absolute;
    top: 50px;
    right: 0;
  }
}
