.oformlenie__button {
  width: fit-content;
  margin: 0 auto;

  text-decoration: none;
}

.oformlenie__text {
  width: 80%;
  max-width: 594px;
  margin: 0 auto;
  padding: 40px 0;

  text-align: center;

  font-size: 16px;
  line-height: 1.3em;
}

@media(min-width:$widthDesktop) {
  .oformlenie__text {
    padding: 88px 0 66px 0;

    font-size: 24px;
  }
}
