.footer {
  position: relative;

  padding-top: 40px;

  background-color: $black;

  &::before {
    position: absolute;
    top: 0;

    width: 100%;
    height: 1px;

    content: '';

    background-color: $white;
  }

  &::after {
    position: absolute;
    top: 7px;

    width: 100%;
    height: 1px;

    content: '';

    background-color: $white;
  }
}

.footer__address {
  margin-bottom: 30px;

  text-align: center;

  font-size: 12px;
}

.footer__tel {
  position: relative;

  display: inherit;

  margin-bottom: 100px;

  text-align: center;
  text-decoration: none;

  font-size: 14px;
  font-weight: 700;

  color: $white;

  &::before {
    position: absolute;
    top: 30px;
    left: 50%;

    width: 65px;
    height: 46px;

    content: '';
    transform: translateX(-50%);

    background: url('../img/tel.svg') 0 0 no-repeat;
  }
}

.footer__right,
.footer__logo {
  display: none;
}

.footer__link {
  &:hover,
  &:focus {
    color: $orange;
  }
}

.footer__tel {
  &:hover,
  &:focus {
    color: $orange;
  }
}

@media(min-width:$widthTablet) {
  .footer {
    padding-top: 11px;
    padding-bottom: 20px;

    &::after {
      display: none;
    }
  }

  .footer__left {
    display: flex;

    align-items: center;
    gap: 30px;
  }

  .footer__container {
    display: flex;

    gap: 20px;
    align-items: center;
    justify-content: space-between;
  }

  .footer__right {
    display: block;

    max-width: 390px;

    flex-grow: 1;
  }

  .footer__nav {
    display: flex;

    width: 100%;

    justify-content: space-between;
  }

  .footer__link {
    text-decoration: none;

    color: $white;
  }

  .footer__mini-container {
    display: flex;
    flex-direction: column;

    flex-wrap: wrap;
  }

  .footer__tel {
    margin-bottom: 0;

    &::before {
      display: none;
    }
  }

  .footer__logo {
    display: block;

    svg {
      fill: $white;
    }

    &:hover,
    &:focus {
      svg {
        fill: $orange;
      }
    }
  }

  .footer__address {
    margin-bottom: 0;

    text-align: start;

    font-size: 14px;
  }

  .footer__tel {
    margin-bottom: 0;

    text-align: start;

    font-size: 14px;
    font-weight: 400;
  }
}

@media(min-width:$widthDesktop) {
  .footer--index {
    .footer__container {
      display: none;
    }
  }
}
