.actions.container {
  display: flex;

  margin-bottom: 50px;

  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
}

.actions__link {
  display: flex;
  flex-direction: column;

  min-width: 300px;
  max-width: 313px;
  max-height: 308px;
  padding: 10px;

  text-decoration: none;

  color: $black;
  border-radius: 40px;
  background-color: $white;

  align-items: center;
  justify-content: flex-start;
  aspect-ratio: 1/1;
  gap: 5px;

  svg {
    margin-bottom: 15px;

    flex-shrink: 0;
  }
}

.actions__subheading {
  display: block;

  margin: 0;

  text-align: center;
  text-transform: uppercase;

  font-size: 24px;
  font-weight: 700;
  line-height: 1.3em;

  color: $orange;

  &.big {
    font-size: 55px;
    line-height: 0.8em;
  }
}

.actions__text {
  margin: 0;

  text-align: center;

  font-size: 24px;
  font-weight: 700;
  line-height: 1.3em;

  color: #ff0000;
}

@media (min-width: 1024px) {
  .actions__link {
    border: 4px solid $white;

    &:hover,
    &:focus {
      border-color: $orange;
    }
  }

  .actions__subheading {
    font-size: 34px;

    &.big {
      font-size: 84px;
    }
  }

  .actions__text {
    font-size: 34px;

    &.small {
      font-size: 24px;
    }
  }

  .action__link {
    margin-bottom: 85px;
  }
}
