.nav {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;

  overflow: scroll;

  width: 100vw;
  height: 100vh;
  padding-top: 38px;

  transition: all 0.7s ease-in;
  text-align: center;

  background: rgba(0, 0, 0, 0.9);

  // backdrop-filter: blur(4px);
  &.hidden {
    left: -1000px;
  }
}

.nav__cross {
  margin-bottom: 10px;

  font-family: $montserrat;
  font-size: 16px;
  font-weight: 700;

  color: $orange;
  border: none;
  background: transparent;

  span {
    position: relative;

    display: block;

    width: 100%;

    &::before {
      position: absolute;
      bottom: -5px;
      left: 50%;

      width: 90px;
      height: 1px;

      content: '';
      transform: translateX(-50%);

      background-color: $white;
    }
  }

  svg {
    margin-bottom: 15px;

    fill: $orange;
  }

  &:hover,
  &:focus {
    color: $white;

    svg {
      fill: $white;
    }
  }
}

.nav__list-menu {
  position: relative;

  &::before {
    position: absolute;
    bottom: -5px;
    left: 50%;

    width: 90px;
    height: 1px;

    content: '';
    transform: translateX(-50%);

    background-color: $white;
  }
}

.nav__link-menu {
  display: block;

  margin-bottom: 12px;

  text-decoration: none;

  font-size: 16px;
  line-height: 32px;

  color: $white;
}

.nav__link {
  display: block;

  margin-bottom: 12px;

  text-decoration: none;

  font-size: 16px;
  font-weight: 700;
  line-height: 32px;

  color: $white;
}

@media(min-width:$widthDesktop) {
  .nav {
    top: 30px;

    overflow: initial;

    width: 122px;
    height: auto;
    min-height: 203px;
    padding-top: 8px;
    padding-left: 12px;

    background: rgba(196, 196, 196, 0.9);

    backdrop-filter: blur(4px);
  }

  .nav__cross {
    display: none;
  }

  .nav__list-menu {
    display: none;
  }

  .nav__list {
    text-align: start;
  }

  .nav__link {
    margin: 0;

    font-weight: 400;
    line-height: 1.9em;

    color: $black;

    &:hover,
    &:focus {
      color: $orange;
    }
  }
}
