.header {
  width: 100%;
  margin-bottom: 20px;
  padding-top: 22px;
  padding-bottom: 14px;

  background-color: $black;

  &--scroll {
    position: fixed;
    z-index: 5;

    height: auto;
  }

  &--index {
    .header__tel {
      margin-bottom: 0;
    }
  }

  & > .container {
    display: grid;

    height: 93px;

    grid-template-columns: 2fr 1fr 2fr;
    align-items: center;
    justify-content: space-between;
  }
}

.header .menu {
  display: none;
}

.header--index {
  height: 110px;
  margin-bottom: 53px;
}

.header__tel {
  margin-bottom: 6px;

  text-decoration: none;

  color: #ffffff;
}

.header__burger {
  position: relative;

  width: 25px;
  height: 25px;
  padding: 10px;

  cursor: pointer;

  border: none;
  background: transparent;
}

.header__burger.hidden {
  display: none;
}

.header__burger span {
  display: block;

  width: 20px;
  height: 3px;
  margin-bottom: 3px;

  border-radius: 1px;
  background-color: #ffffff;
}

.header__burger span:last-of-type {
  margin-bottom: 0;
}

.header__burger svg {
  position: absolute;
  top: 0;
  left: 0;

  fill: #ffffff;
}

.header__burger:hover svg,
.header__burger:focus svg {
  fill: #f58220;
}

.header__cross {
  cursor: pointer;

  border: none;
  background-color: transparent;
}

.header__cross:hover svg,
.header__cross:focus svg {
  fill: #f58220;
}

.header__cross svg {
  fill: #ffffff;
}

.header__cross.hidden {
  display: none;
}

.header__left {
  position: relative;

  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;

  height: 100%;

  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.header__right {
  position: relative;

  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;

  height: 100%;

  -webkit-box-pack: end;
  -ms-flex-pack: end;
  justify-content: flex-end;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}

.header__logo {
  margin: 0 16px;

  justify-self: center;
}

.header__logo svg {
  -webkit-transition: fill 0.3s ease-in;
  transition: fill 0.3s ease-in;

  fill: #ffffff;
}

.header__logo:hover svg,
.header__logo:focus svg {
  fill: #f58220;
}

.header__order {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;

  width: 60px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;

  text-decoration: none;

  color: #ffffff;

  justify-self: flex-end;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-align: end;
  -ms-flex-align: end;
  align-items: flex-end;
}

.header__order .header__order-svg-hover {
  display: none;
}

.header__order:hover .header__order-svg,
.header__order:focus .header__order-svg {
  display: none;
}

.header__order:hover .header__order-svg-hover,
.header__order:focus .header__order-svg-hover {
  display: block;
}

.header__order-svg {
  display: block;

  width: 25px;
  height: auto;
  margin-bottom: 2px;

  fill: #ffffff;
}

.header__order-svg-hover {
  width: 27px;
  height: auto;

  fill: #f58220;
}

.header__order-text {
  width: 100%;

  text-align: center;

  font-size: 10px;
  line-height: 9.04px;
}

.header__order-text span {
  display: block;

  color: #f58220;
}

.header__left::before,
.header__right::before {
  position: absolute;
  bottom: 7px;
  left: 0;

  width: 100%;
  height: 1px;

  content: '';

  background-color: #ffffff;
}

.header__left::after,
.header__right::after {
  position: absolute;
  bottom: 18px;
  left: 0;

  width: 100%;
  height: 1px;

  content: '';

  background-color: #ffffff;
}

@media (max-width: 1023px) {
  .header__tel {
    display: none;
  }
}

@media (min-width: 1024px) {
  .header {
    padding-bottom: 36px;

    & > .container {
      height: 175px;
    }

    .menu {
      display: grid;
    }

    &--scroll {
      padding: 5px 0;

      box-shadow: 0 0 4px $orange;

      & > .container {
        height: 80px;
      }

      .menu--inner {
        display: none;
      }

      .header__left,
      .header__order {
        align-items: center;
      }

      .header__logo svg {
        width: 60px;
      }

      .header__order-svg {
        width: 40px;
      }
    }

    &--index {
      .container {
        height: 300px;
      }
    }
  }

  .header--index {
    min-height: 367px;
  }

  .header--index .header__left,
  .header--index .header__right {
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
  }

  .header__order {
    width: auto;
    height: 62px;
  }

  .header__burger-container {
    position: relative;

    height: 32px;
  }

  .header__burger,
  .header__cross {
    width: 22px;
    height: 22px;
    margin-right: 34px;
    padding: 0;
  }

  .header__burger svg,
  .header__cross svg {
    width: 100%;
    height: auto;
  }

  .header__order-text {
    width: auto;

    font-size: 14px;
    line-height: 17px;
  }

  .header__order-text span {
    text-align: start;
  }

  .header__order-svg {
    width: 63px;
    height: auto;
    margin-right: 8px;
  }

  .header__order-svg-hover {
    width: 68px;
    height: auto;
  }

  .header__left,
  .header__right {
    -webkit-box-align: end;
    -ms-flex-align: end;
    align-items: end;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
  }

  .header__left::before,
  .header__right::before {
    bottom: 35px;

    display: none;
  }

  .header__left::after,
  .header__right::after {
    display: none;
  }

  .header--index .header__left::before,
  .header--index .header__left::after,
  .header--index .header__right::before,
  .header--index .header__right::after {
    display: block;
  }

  .header__logo svg {
    width: 148px;
    height: auto;
  }

  .header__logo--index svg {
    width: 253px;
    height: auto;
  }
}
