.delivery__subheading {
  margin: 0;
  margin-bottom: 10px;

  font-weight: 700;
  line-height: 1.2em;

  color: $orange;

  span {
    display: block;
  }
}

.delivery__text {
  margin: 0;
  margin-bottom: 32px;

  line-height: 1.3em;

  span {
    display: block;
  }

  a {
    text-decoration: none;

    color: $white;

    &:hover,
    &:focus {
      color: $orange;
    }
  }

  .delivery__subheading {
    margin-bottom: 0;
    margin-bottom: 10px;

    text-transform: uppercase;

    font-size: 16px;
  }
}

@media(min-width:$widthDesktop) {
  .delivery.container {
    display: grid;

    padding-bottom: 85px;

    grid-template-columns: 1fr 1fr 1fr;
    justify-content: space-between;
  }

  .delivery__item {
    padding: 0 14%;
    padding-top: 10px;

    &:nth-of-type(2) {
      border-right: 1px solid $orange;
      border-left: 1px solid $orange;
    }
  }

  .delivery__subheading {
    margin-bottom: 32px;

    font-size: 24px;
  }

  .delivery__text {
    font-size: 16px;
  }
}
