.count {
  display: flex;
}

.count__button {
  display: flex;

  width: 40px;
  height: 44px;
  padding: 0;

  cursor: pointer;

  font-size: 36px;

  color: $white;
  border: 1px solid $white;
  border-radius: 5px;
  background-color: $black;

  align-items: center;
  justify-content: center;

  &:hover,
  &:focus {
    background-color: $orange;
  }
}

.count__number {
  display: flex;

  width: 40px;
  height: 44px;
  margin: 0 13px;
  padding-left: 7px;

  text-align: center;

  font-family: $montserrat;
  font-size: 24px;

  color: $white;
  border: 1px solid $white;
  border-radius: 5px;
  background-color: $black;

  flex-shrink: 0;
  align-items: center;
  justify-content: center;
  -moz-appearance: textfield;

  &::-webkit-outer-spin-button,
  &::-webkit-inner-spin-button {
    -webkit-appearance: none;
  }
}

.count__less {
  padding-bottom: 7px;
}

@media(min-width:$widthDesktop) {
  .count__button {
    width: 30px;
    height: 30px;
  }

  .count__number {
    width: 40px;
    height: 30px;
  }
}
