  *,
*::before,
*::after {
  box-sizing: inherit;
}

* {
  -webkit-tap-highlight-color: transparent;
}

html {
  box-sizing: border-box;

  color: $white;
  background-color: $black;

  scroll-behavior: smooth;
}

body {
  display: flex;
  flex-direction: column;

  height: 100vh;
  margin: 0;

  font-family: $montserrat;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.4em;

  justify-content: space-between;
}

img {
  max-width: 100%;
  height: auto;
}

.main {
  flex-grow: 1;

  &.inner {
    padding-top: 30px;
  }
}

.visually-hidden:not(:focus):not(:active),
input[type='checkbox'].visually-hidden,
input[type='radio'].visually-hidden {
  position: absolute;

  overflow: hidden;
  clip: rect(0, 0, 0, 0);

  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;

  white-space: nowrap;

  border: 0;

  clip-path: inset(100%);
}

.container {
  width: 100%;
  max-width: 1030px;
  margin: 0 auto;
  padding: 0 15px;
}

.yandex-map,
.yandex-map .iframe {
  width: 100%;
  height: 620px;
  margin: 0 auto;
  padding: 0;
}

.no-scroll {
  overflow: hidden;
}

.button {
  display: flex;

  padding: 14px 24px;

  cursor: pointer;

  font-family: $montserrat;
  font-size: 18px;

  color: $black;
  border: none;
  border-radius: 6px;
  border-radius: 5px;
  background-color: $white;

  justify-content: center;
  align-items: center;

  &:hover,
  &:focus,
  &:active {
    color: $black;
    background-color: $white;
  }
}

.checkbox-input {
  position: absolute;

  opacity: 0;

  flex-shrink: 0;

  &:checked + .checkbox-indicator {
    background: url(../img/checkbox-checked.svg) 0 0 no-repeat;
  }
}

.checkbox-indicator {
  display: block;

  width: 20px;
  height: 20px;
  margin-right: 8px;

  cursor: pointer;

  background: url(../img/checkbox.svg) 0 0 no-repeat;
}

.inner-content {
  .title,
  h1 {
    font-size: 26px;
    font-weight: 800;
  }

  h2 {
    font-size: 20px;
    font-weight: 800;
  }

  h3 {
    font-size: 18px;
    font-weight: 800;
  }

  h4 {
    font-size: 18px;
    font-weight: 600;
  }

  h5 {
    font-size: 14px;
    font-weight: 700;
  }

  p {
    margin: 1em 0;

    font-size: 14px;
    line-height: 18.2px;
  }

  a {
    text-decoration: none;

    color: $white;
  }

  .button {
    color: $white;
  }
}

.input {
  width: 100%;
  padding: 8.5px 14px;

  border: 0;
  border-radius: 50px;
  background-color: #f2f2f2;
}

.overflow-hidden {
  overflow: hidden;
}

.section {
  margin-bottom: 50px;
}

.title {
  position: relative;

  margin-top: 20px;
  margin-bottom: 19px;

  text-align: center;

  font-size: 18px;
  font-weight: 400;

  span {
    &:nth-of-type(2) {
      display: none;
    }
  }
}

.bg {
  width: 100%;
  height: 266px;
  margin-bottom: 70px;

  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.radio-input {
  position: absolute;

  opacity: 0;

  flex-shrink: 0;

  &:checked + .radio-indicator {
    background: url(../img/checkbox-checked.svg) 0 0 no-repeat;
    background-color: $orange;
  }
}

.radio-indicator {
  display: block;

  width: 20px;
  height: 20px;
  margin-right: 2em;

  cursor: pointer;

  border-radius: 50%;
  background-color: #a3a3a3;

  flex-shrink: 0;
}

.delivery {
  transition: all 0.3s ease-in;

  opacity: 1;

  &.hidden {
    position: absolute;
    bottom: -100px;

    opacity: 0;
  }
}

@media(max-width:$widthTablet) {
  .bg {
    display: none;
  }
}

@media(min-width:$widthTablet) {
  .container {
    padding: 0 20px;
  }

  .section {
    margin-bottom: 100px;
  }

  .title {
    margin-bottom: 27px;

    font-size: 30px;

    span {
      display: none;

      &:nth-of-type(2) {
        display: block;
      }
    }
  }

  .overflow-hidden {
    overflow: initial;
  }
}

@media(min-width:$widthDesktop) {
  .button {
    font-size: 24px;
  }

  .main {
    &.inner {
      padding-top: 80px;
    }
  }

  .title {
    margin-top: 0;
  }
}
