.order {
  padding-top: 20px;

  color: $black;
  background-color: #e5e5e5;
}

.order__container {
  padding-bottom: 30px;
}

.order__section {
  padding: 40px 0;

  &:first-of-type {
    padding-top: 0;
  }
}

.order__label {
  display: block;

  width: 100%;
  margin-bottom: 20px;

  cursor: pointer;

  align-items: center;

  &:hover,
  &:focus {
    color: $orange;
  }

  &:last-of-type {
    margin-bottom: 0;
  }
}

.order__title {
  margin-bottom: 1.5em;

  font-size: 18px;
}

.order__input {
  width: 100%;
  padding: 10px;

  font-family: $montserrat;
  font-size: 18px;

  border: 1px solid #a3a3a3;
  background-color: transparent;

  &.date {
    font-family: $montserrat;
  }

  &.time {
    font-family: $montserrat;
  }
}

.order__radio {
  display: flex;
}

.order__label {
  &--half {
    display: inline-block;

    width: calc(100%/2 - 20px);
    margin-right: 0;

    &:nth-of-type(2n) {
      margin-right: 10px;
    }
  }
}

.order__label-text {
  display: block;

  width: 100%;
  margin-bottom: 10px;
}

.order__center-section {
  display: flex;

  align-items: center;

  .order__label {
    margin-bottom: 0;
  }

  .radio-indicator {
    margin-right: 10px;
  }
}

.order__sub-section {
  padding: 20px 0;
}

.order__submit-section {
  padding: 30px 0;

  color: $white;
  background-color: $black;

  & > .container {
    display: flex;

    flex-wrap: wrap;
    gap: 30px;
    align-items: center;
    justify-content: center;
  }

  a {
    text-decoration: none;

    color: $white;

    &:hover,
    &:focus {
      color: $orange;
    }
  }

  .order__label {
    max-width: 900710px;
  }
}

.order__submit {
  &:hover,
  &:focus {
    color: $white;
    background-color: $orange;
  }
}

@media(max-width:1023px) {
  .order__container {
    max-width: 500px;
  }
}

@media(min-width:$widthDesktop) {
  .order {
    padding-top: 40px;

    font-size: 16px;
  }

  .order__submit-section {
    & > .container {
      flex-wrap: nowrap;
      justify-content: space-between;
    }
  }

  .order__submit {
    padding: 5px 30px;

    font-size: 24px;
  }

  .order__section {
    display: grid;

    grid-gap: 104px;
    grid-template-columns: 1fr 1fr;
  }

  .order__title {
    font-size: 24px;
  }

  .order__input {
    font-size: 24px;
  }

  .order__center-section {
    gap: 70px;

    .order__label {
      display: flex;

      width: auto;

      align-items: center;
    }
  }
}
