.menu {
  display: grid;

  width: 100%;

  grid-template-columns: 1fr 1fr 1fr 1fr;

  &--index {
    justify-items: center;
    align-items: baseline;
    grid-template-columns: 1fr 1fr;
    grid-row-gap: 40px;
    grid-column-gap: 48px;

    .menu__item {
      font-size: 14px;
      line-height: 17px;

      svg {
        margin-bottom: 10px;
      }
    }

    @media(min-width:$widthTablet) {
      .menu {
        grid-template-columns: 1fr 1fr 1fr;
      }

      .menu__item {
        svg {
          margin-bottom: 29px;

          transform: scale(1.3);
        }
      }
    }

    @media(min-width:$widthDesktop) {
      padding-bottom: 91px;

      grid-template-columns: 1fr 1fr 1fr 1fr;
      grid-column-gap: 30px;
      align-items: start;

      .menu__item {
        display: flex;

        width: 100%;
        height: auto;

        border: 1px solid $black;

        align-items: end;
        flex-wrap: wrap;
        aspect-ratio: 1/1;
        justify-content: center;

        &:hover,
        &:focus {
          color: $orange;
          border: 1px solid $orange;

          svg {
            fill: $orange;
          }
        }

        svg {
          height: auto;
          margin-bottom: 0;
        }

        &:nth-of-type(1) {
          svg {
            width: 91px;
          }
        }

        &:nth-of-type(2) {
          svg {
            width: 94px;
          }
        }

        &:nth-of-type(3) {
          svg {
            width: 118px;
          }
        }

        &:nth-of-type(4) {
          svg {
            width: 96px;
          }
        }

        &:nth-of-type(5) {
          svg {
            width: 84px;
          }
        }

        &:nth-of-type(6) {
          svg {
            width: 68px;
          }
        }

        &:nth-of-type(7) {
          svg {
            width: 84px;
          }
        }

        &:nth-of-type(8) {
          svg {
            width: 41px;
          }
        }

        span {
          font-size: 18px;
          line-height: 22px;
        }
      }
    }
  }
}

.menu__item {
  display: block;

  transition: all 0.3s ease-in;
  text-align: center;
  text-decoration: none;

  color: $white;

  align-items: center;

  svg {
    margin-bottom: 7px;

    transition: all 0.3s ease-in;

    fill: $white;
  }

  span {
    display: block;

    width: 100%;
    min-height: 52px;
  }

  &:hover,
  &:focus {
    color: $orange;

    svg {
      fill: $orange;
    }
  }
}
